import tuiTopDropdown from "@/components/top-dropdown/top-dropdown"
import listItem from "@/colorui/components/listitem"
var that;
export default {
	components: {
		tuiTopDropdown,
		listItem
	},
	filters: {
		km: function(val) {
			if (val != null) {
				return (Number(val) / 1000).toFixed(2) + 'km';
			} else {
				return "";
			}
		}
	},
	data() {
		return {
			seller: false,
			isLoad: true,
			cardCur: 0,
			tabIndex: 1, //顶部筛选索引
			selectedName: ['报价中', '距离', '服务类型'],
			selectH1: 0,
			selectH2: 0,
			selectH3: 0,
			dropdownList1: [{
				value: 1,
				name: "报价中",
				selected: false
			}, {
				value: 2,
				name: "服务中",
				selected: false
			}, {
				value: 3,
				name: "服务完成",
				selected: false
			}],
			dropdownList2: [{
				value: 0,
				name: "距离",
				selected: false
			}, {
				value: 1,
				name: "距离最近",
				selected: false
			}, {
				value: 2,
				name: "距离最远",
				selected: false
			}],
			getTaskCategoryList: [],
			getTaskList: [],
			form: {
				latitude: 0, //纬度
				longitude: 0, //经度
				keywords: '', //搜索关键词
				sort_1: 0, //综合排序：0默认1人气最高2最新上架
				sort_2: 0, //距离排序：0默认 1距离最近 2距离最远
				sort_3: 0, //类型筛选：0默认 ,分类对应的id
				page: 1,
				size: 10
			},
			location: '定位中',
			avatarUrl: '',
		};
	},
	onLoad(e) {
		that = this;
		this.dropdownList1[0].selected = true;
		this.dropdownList2[0].selected = true;
		this.avatarUrl = uni.getStorageSync('headimg')
		this.$api.post(this.api.getTaskCategoryList,
			function callbacks(res) {
				that.getTaskCategoryList = res.data
				for (let i = 0; i < that.getTaskCategoryList.length; i++) {
					that.$set(that.getTaskCategoryList[i], 'selected', false)
				}
			})
		this.form.longitude = this.long
		this.form.latitude = this.lat
		that.isOpenRefresh(true);
		if (this.lat != 0) {
			this.$api.get(
				'https://api.map.baidu.com/reverse_geocoding/v3/?ak=Ulna6HAk7FsVuGeG4aOsV1wWZtxcosrC&output=json&coordtype=wgs84ll&location=' +
				this.lat + ',' + this.long,
				function callbacks(res) {
					that.location = res.result.addressComponent.city;
				})
		}
	},
	methods: {
		swithTab() {
			uni.switchTab({
				url:'../mine/personal'
			})
		},
		searchBykeywords(event) {
			this.form.keywords = event.detail.value;
			this.isOpenRefresh(true)
		},
		checked(item, child) {
			let items = item;
			console.log(item)
			for (var i = 0; i < items.length; i++) {
				if (child == i) {
					items[i].selected = true;
				} else {
					items[i].selected = false;
				}
			}
		},
		px(num) {
			return uni.upx2px(num) + "px"
		},
		showDropdownList: function() {
			if (this.tabIndex == 1) {
				this.selectH1 = 400;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 2) {
				this.selectH1 = 0;
				this.selectH2 = 400;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 3) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 300;
			}
		},
		hideDropdownList: function() {
			if (this.tabIndex == 1) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 2) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
			if (this.tabIndex == 3) {
				this.selectH1 = 0;
				this.selectH2 = 0;
				this.selectH3 = 0;
			}
		},
		dropdownItem2: function(e) {
			console.log(e.currentTarget.dataset)
			let data = e.currentTarget.dataset;
			this.selectedName[0] = data.name
			for (let i = 0; i < this.dropdownList1.length; i++) {
				this.dropdownList1[i].selected = false;
			}
			this.dropdownList1[data.index].selected = true;
			this.form.sort_1 = data.value
			this.selectH1 = 0
			this.isOpenRefresh(true);
		},
		dropdownItem4: function(e) {
			console.log(e.currentTarget.dataset)
			let data = e.currentTarget.dataset;
			this.selectedName[1] = data.name
			for (let i = 0; i < this.dropdownList2.length; i++) {
				this.dropdownList2[i].selected = false;
			}
			this.dropdownList2[data.index].selected = true;
			this.form.sort_2 = data.value
			this.selectH2 = 0
			this.isOpenRefresh(true);
		},
		dropdownItem5: function(e) {
			console.log(e.currentTarget.dataset)
			let data = e.currentTarget.dataset;
			this.selectedName[2] = data.name
			for (let i = 0; i < this.getTaskCategoryList.length; i++) {
				this.getTaskCategoryList[i].selected = false;
			}
			this.getTaskCategoryList[data.index].selected = true;
			this.form.sort_3 = data.value
			this.selectH3 = 0
			this.isOpenRefresh(true);
		},
		screen: function(e) {
			let index = e.currentTarget.dataset.index;
			console.log(index)
			this.tabIndex = index;
			if (index == 1) {
				if (this.selectH1 == 0) {
					this.showDropdownList();
				} else {
					this.hideDropdownList();
				}
			}
			if (index == 2) {
				if (this.selectH2 == 0) {
					this.showDropdownList();
				} else {
					this.hideDropdownList();
				}
			}
			if (index == 3) {
				if (this.selectH3 == 0) {
					this.showDropdownList();
				} else {
					this.hideDropdownList();
				}
			}
		},
		isOpenRefresh(isSupport) {
			this.isLoad = true;
			if (isSupport) {
				this.getTaskList = [];
				this.form.page = 1;
			}
			let data = {
				keywords: this.form.keywords,
				latitude: this.form.latitude,
				longitude: this.form.longitude,
				sort_1: this.form.sort_1,
				sort_2: this.form.sort_2,
				sort_3: this.form.sort_3,
				page: this.form.page,
				size: this.form.size
			}
			this.$api.postWithData(this.api.getTaskList, data,
				function callbacks(res) {
					uni.stopPullDownRefresh();
					if (res.data.length > 0) {
						that.isLoad = true;
					} else {
						that.isLoad = false;
					}
					for (let i = 0; i < res.data.length; i++) {
						that.getTaskList.push(res.data[i]);
					}
					for (var i = 0; i < that.getTaskList.length; i++) {
						that.getTaskList[i].image = that.getTaskList[i].image == '' ? '../../static/logo.jpg' : that.getTaskList[i].image
							.split(",")[0]
					}
					if (that.getTaskList.length <= 5) {
						that.isLoad = false;
					}
				})
			// const pages = getCurrentPages();
			// const page = pages[pages.length - 1];
			// const currentWebview = page.$getAppWebview()
			// currentWebview.setStyle({
			// 	pullToRefresh: {
			// 		support: isSupport,
			// 		style: plus.os.name === 'Android' ? 'circle' : 'default'
			// 	}
			// });
		},
		navigate(item) {
			let uid = uni.getStorageSync("uid");
			that.$api.post(that.api.isSeller,
				function callbacks(res) {
					if (res.code == 2) {
						that.seller = true
					} else {
						that.seller = false
					}
					if (item.uid == uid || that.seller) {
						console.log(item.uid);
						uni.navigateTo({
							url: page
						})
					} else {
						that.$api.msg("仅限发布者和商家可查看详情")
					}
				})
		}
	},
	onPullDownRefresh() {
		this.isOpenRefresh(true);
	},
	onReachBottom() {
		this.form.page++;
		this.isOpenRefresh(false);
	},
	onHide() {
		console.log("onHide")
	}
}
